import { Component, Host, h, Event, EventEmitter } from '@stencil/core';
import { widgetState } from '../../store';

@Component({
  tag: 'charma-products',
  styleUrl: 'charma-products.scss',
  shadow: true,
})
export class CharmaProducts {
  @Event() productSelect: EventEmitter<string>;

  render() {
    return (
      <Host>
        <div class="grid gap-5 grid-cols-1 md:grid-cols-2 lg:grid-cols-3">
          {widgetState.products.map(product => {
            return (
              <charma-card
                key={product.id}
                id={product.id}
                selected={widgetState.selectedProducts.includes(product.id)}
                onClick={() => {
                  this.productSelect.emit(product.id);
                  widgetState.error = undefined;
                }}
                product={product}
              ></charma-card>
            );
          })}
        </div>
      </Host>
    );
  }
}
